*{
    padding:0px;
    margin:0px;
  }
  button:focus{
    outline:none;
  }
  .box{
    min-width:640px;
    background:#C0C0C0;
    border-width:1px;
    border-color:#FFFFFF #808080 #808080 #FFFFFF;
    border-style:solid;
    position:absolute;
    /* top:30px;
    left:30px; */
    padding:1px;
  }
  .box-minimized{
    height:18px !important;
    overflow:hidden !important;
  }
  .box-full{
    width:calc(100% - 31px);
    height:calc(100% - 30px);
    left:0px !important;
    top:18px !important;
  }
  div.title{
    height:18px;
    width:100%;
    background:#000080;
    color:#FFF;
  }

  img.title{
    float:left;
  }
  p.title{
    margin:2px 0 0 1px;
    float:left;
    font-family:Tahoma;
    font-weight:bold;
    font-size:11px;
  }
  .title button{
    /*position:absolute;
    right:3px;
    top:3px;*/
    margin:2px 2px 2px 0;
    float:right;
    width:14px;
    height:13.5px;
    background:#C0C0C0;
    border-width:1px;
    border-color:#FFFFFF #808080 #808080 #FFFFFF;
    padding:0;
    font-size:9px;
    font-weight:bold;
    font-family:Tahoma;
    text-align:center;
  }
  div.body{
    height:100%;
    width:100%;
    display:inline-block;
    position:relative;
  }
  div.body p{
    font-family:"MS Sans Serif";
    font-size:11px;
  }
 
  div.body button{
    font-family:"MS Sans Serif";
    font-size:11px;
    outline:1px solid #000000;
    background:#C0C0C0;
    border-width:1px;
    border-style:solid;
    border-color:#FFFFFF #808080 #808080 #FFFFFF;
    padding:4px 10px;
    margin:20px auto 3px;
    display:block;
    position:relative;
    bottom:0px;
  }